export default {
	namespaced: true,
	state: {
		history: [],
	},
	getters: {
		wholeHistory: state => state.history,
	},
	mutations: {
		addEventToHistory(state, event) {
			state.history.push(event);
		},
		setHistory(state, elem) {
			state.history = elem;
		}
	},
	actions: {
		initHistory(store) {
			if (!localStorage.getItem('appHistory')) {
				store.commit('setHistory', []);
				let appHistory = JSON.stringify(store.state.history);
				localStorage.setItem('appHistory', appHistory);
			} else {
				let historyData = JSON.parse(localStorage.getItem('appHistory'));
				store.commit('setHistory', historyData);
			}
		},
		addToHistory( store ,event) {
			store.commit('addEventToHistory', event);
			let appHistory = JSON.stringify(store.state.history);
			localStorage.setItem('appHistory', appHistory);
		},
		resetHistory(store) {
			store.commit('setHistory', []);
			let appHistory = JSON.stringify(store.state.history);
			localStorage.setItem('appHistory', appHistory);
		}
	}
}