<template>
  <div class="home">
    <div class="container">
      <div class="links">
        <router-link :to="{ name: 'History'}" class="web-link">История добавлений</router-link>
        <input type="text" v-model="serchValue" class="search-input" placeholder="поиск...">
        <router-link to="/choosen" class="choosen-span">Избранное</router-link>
      </div>
    <div class="movies-container">
      <transition-group tag="div" name="filmes" class="movies-blocks">
        <movie-block v-for="item in searchFilms" :key="item.filmId" :add="add" :movie="item" @click="onMovie(item)" />
      </transition-group>
      <div class="favorites">
        <h3 class="favorites-title">
          Избранное
          <div class="favorites-span">Добавь сюда фильмы, которые хочешь посмотреть</div>
        </h3>
        <div class="favorites-block" v-show="allFavorites.length > 0">
          <transition-group tag="div" name="filmes" class="favorites-item" appear>
              <movie-block v-for="favorite in allFavorites" :key="favorite.filmId" :movie="favorite" :add="remove" @click="removeMovie(favorite.filmId)" />
          </transition-group>
        </div>
      </div>
    </div>  
    </div>
  </div>
</template>

<script>
  
  import MovieBlock from '@/components/MovieBlock'
  import {mapGetters, mapActions} from 'vuex';

export default {
  name: 'Home',
  data() {
      return {
         add: false,
         remove: true,
         serchValue: ''
      }
  },
  components: {
    MovieBlock
  },
  computed: {
    ...mapGetters('movies', { all: 'all'} ),
    ...mapGetters('movies', { pagesCount: 'pagesCount'} ),
    ...mapGetters('favorites', { allFavorites: 'allFavorites'} ),
    searchFilms() {
        return this.all.filter(film => film.nameRu.toLowerCase().includes(this.serchValue));
    }
  }, 
  methods: {
    ...mapActions('favorites', { addToFavorites: 'addToFavorites' }),
    ...mapActions('favorites', { removeFromFavorites: 'removeFromFavorites' }),
    ...mapActions('favorites', { findItem: 'findItem' }),
    ...mapActions('movies', { removeFromFilms: 'removeFromFilms' }),
    ...mapActions('movies', { addToFilms: 'addToFilms' }),
    ...mapActions('history', { addToHistory: 'addToHistory' }),
    onMovie(movie) {
      this.addToFavorites(movie);
      this.removeFromFilms(movie.filmId);
      let date = new Date().toLocaleString();
      this.addToHistory({
            date: date,
            name: movie.nameRu,
            type: 'added'
       });
    },
    removeMovie(id) {
       let returnedMovie = JSON.parse(JSON.stringify(this.allFavorites.find(item => item.filmId === id)));
       this.removeFromFavorites(id);
       this.addToFilms(returnedMovie);
       let date = new Date().toLocaleString();
       this.addToHistory({
            date: date,
            name: returnedMovie.nameRu,
            type: 'removed'
       });
    },
  },
};
</script>

<style>
  .filmes-enter-from {
      transform: scale(0);
  }
  .filmes-enter-to {
      transform: scale(1);
  }
  .filmes-enter-active {
    transition: all 0.6s ease-out;
  }
  .filmes-leave-from {
      transform: scale(1);;
  }
  .filmes-leave-to {
      transform: scale(0);;
  }
  .filmes-leave-active {
    transition: all 0.6s ease-out;
    position: absolute;
  }

  .filmes-move {
    transition: all 0.6s ease;
  }

</style>