export default {
	namespaced: true,
	state: {
		favorites: []
	},
	getters: {
		allFavorites: state => state.favorites,
	},
	mutations: {
		setFavorites(state, favorites) {
			state.favorites = favorites;
		},
		addFavorite(state, favorite) {
			state.favorites.push(favorite);
		},
		removeItem(state, id) {
			state.favorites = state.favorites.filter(favorite => favorite.filmId !== id);
		},
		findFavorite(state, id) {
			let searchedItem = state.favorites.find(favorite => favorite.filmId = id);
			return searchedItem;
		}
	},
	actions: {
		loadFavorites(store) {
			if (!localStorage.getItem('favorites')) {
				store.commit('setFavorites', []);
				let initFavorites = JSON.stringify(store.state.favorites);
				localStorage.setItem('favorites', initFavorites);
			} else {
				let favoritesData = JSON.parse(localStorage.getItem('favorites'));
				store.commit('setFavorites', favoritesData);
			}
		},
		addToFavorites(store, item) {
			store.commit('addFavorite', item);
			let storageFavorites = JSON.stringify(store.state.favorites);
			localStorage.setItem('favorites', storageFavorites);
		},
		removeFromFavorites(store, id) {
			store.commit('removeItem', id);
			let deletedFavorites = JSON.stringify(store.state.favorites);
			localStorage.setItem('favorites', deletedFavorites);
		},
		findItem(store, id) {
			store.commit('findFavorite', id);
		}
	}
}