<template>
	<div class="block" :key="movie.filmId">
        <img :src="movie.posterUrl" class="block-img">
        <div class="block-body">
          <h2 class="block-title">{{movie.nameRu.split('(')[0]}}</h2>
          <p>
            <span v-for="genre in movie.genres" class="block-span">
              {{genre.genre}}
            </span>
          </p>
          <button class="btn add-btn" v-show="!add">
            Добавить в избранное
          </button>
          <button class="btn remove-btn" v-show="add">
            Убрать из избранных
          </button>
        </div>
    </div>
</template>

<script>

	
	export default {
         name: 'MovieBlock',
         props: {
         	movie: {
         		type: Object,
         		required: true
         	},
          add: {
            type: Boolean,
            default: true
          }
         },
	};

</script>