import { makeRequest } from '@/api/server';

export default {
	namespaced: true,
	state: {
		films: [],
		currentPage: 1,
		mainUrl: 'https://kinopoiskapiunofficial.tech/api/v2.2/films/top?type=TOP_100_POPULAR_FILMS&page='
	},
	getters: {
		all: state => state.films,
		pagesCount: state => state.pagesCount,
	},
	mutations: {
		setFilms(state, films) {
			state.films = films;
		},
		setPagesCount(state, pages) {
			state.currentPage = pages;
		},
		removeFilm(state, id) {
			state.films = state.films.filter(film => film.filmId !== id);
		},
		addFilm(state, film) {
			state.films.unshift(film);
		}
	},
	actions: {
		async load(store) {
			if (!localStorage.getItem('filmList')) {
				let data = await makeRequest(`${store.state.mainUrl}${store.state.currentPage}`);
				let films = data.films;
				let pagesCount = data.pagesCount;
				store.commit('setFilms', films);
				store.commit('setPagesCount', pagesCount);
				let jsonFilms = JSON.stringify(films);
				localStorage.setItem('filmList', jsonFilms);
			} else {
				let jsFilms = JSON.parse(localStorage.getItem('filmList'));
				store.commit('setFilms', jsFilms);
			}
		},
		removeFromFilms(store, id) {
			store.commit('removeFilm', id);
			let deletedFilms = JSON.stringify(store.state.films);
			localStorage.setItem('filmList', deletedFilms);
		},
		addToFilms(store, film) {
			store.commit('addFilm', film);
			let storageFilms = JSON.stringify(store.state.films);
			localStorage.setItem('filmList', storageFilms);
		}
	}
}